.page__body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.page__stopwatch {
  position: relative;
}

@keyframes rotation {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
}

.stopwatch__minutes {
  z-index: -1;
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite rotation;
  position: absolute;
  bottom: 40vmin;
}

.stopwatch__seconds {
  z-index: -1;
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite rotation;
  position: absolute;
  bottom: 40vmin;
}

.stopwatch__circle {
  background-color: #f6a603;
  border-radius: 50%;
  align-self: center;
  width: 5vmin;
  height: 5vmin;
}

.stopwatch--speed-up .stopwatch__seconds {
  animation-duration: 10s;
}

.stopwatch--speed-up .stopwatch__minutes {
  animation-duration: 600s;
}
